.anim-start .menu__line--top {
  -webkit-animation: line-top 0.5s ease-in-out forwards;
          animation: line-top 0.5s ease-in-out forwards; }

.anim-start .menu__line--middle {
  -webkit-animation: line-middle 0.5s ease-in-out forwards;
